<template>
	<div class="login-page">
	  	<el-row type="flex" class="login-header" justify="space-between">
			<!-- <el-col :span="6" class="login-header-left">{{$t('system')}}</el-col> -->
			<el-col :span="12" class="login-header-left">{{$t("loginTitle")}}</el-col>
		    <el-col :span='6'>
			    <el-dropdown class='login-header-dropDown' @command='switchLanguage' @visible-change='languageDropdown=!languageDropdown'>
					<span class="el-dropdown-link" role='button'>{{$t("language")}}
						<i :class="[languageDropdown ? 'el-icon-caret-bottom' : 'el-icon-caret-top', 'el-icon--right']"></i>
					</span>
					<el-dropdown-menu slot="dropdown">
						<el-dropdown-item command='Zh'>简体中文</el-dropdown-item>
						<el-dropdown-item command='En'>English</el-dropdown-item>
						<el-dropdown-item command='Ft'>繁體中文</el-dropdown-item>
					</el-dropdown-menu>
				</el-dropdown>
		    </el-col>
		</el-row>

    	<div class="login-regist-screen">
			<div class="login-regist-window" v-show="loginCtrl==='login'">
				<div class="login-regist-title">{{$t('login.title')}}</div>
				<el-form :model="users" ref="users" :rules='usersRules' class="login-regist-form">
					<div class="login-regist-input-wrap">
						<div class="email-icon"></div>
						<el-form-item prop="username">
							<el-input type="text" v-model="users.username" auto-complete="off" :placeholder="$t('login.user')" @keyup.enter.native="loginSubmit('users')" autofocus></el-input>
						</el-form-item>
					</div>
					<div class="login-regist-input-wrap">
						<div class="password-icon"></div>
						<el-form-item prop="password">
							<el-input type="password" v-model="users.password" auto-complete="off" :placeholder="$t('login.password')" @keyup.enter.native="loginSubmit('users')"></el-input>
						</el-form-item>
					</div>
					<div class="new-user-regist">
					    <span @click="registUser('users')">{{$t('login.regist')}}</span><span @click="findPwd('users')">{{$t('login.retrievePwd')}}</span>
					</div>
					<el-form-item>
				    	<el-button type="success" @click="loginSubmit('users')" class="login-regist-submit-btn">{{$t('login.login')}}</el-button>
					</el-form-item>
			 </el-form>
			</div>


			<div class="login-regist-window" v-show="loginCtrl==='regist'">
				<div class="login-regist-title">{{$t('register.title')}}</div>
				<el-form :model="registValidate" ref="registValidate" :rules='registRules' class="login-regist-form">
					<div class="login-regist-input-wrap">
						<div class="user-icon"></div>
						<el-form-item prop="name">
							<el-input type="text" v-model="registValidate.name" auto-complete="off" :placeholder="$t('register.user')" @keyup.enter.native="registSubmit('registValidate')"></el-input>
						</el-form-item>
					</div>
					<div class="login-regist-input-wrap">
						<div class="email-icon"></div>
						<el-form-item prop="email">
							<el-input type="text" v-model="registValidate.email" auto-complete="off" :placeholder="$t('register.email')" @keyup.enter.native="registSubmit('registValidate')"></el-input>
						</el-form-item>
					</div>
					<div class="login-regist-input-wrap">
						<div class="password-icon"></div>
						<el-form-item prop="pass">
							<el-input type="password" v-model="registValidate.pass" auto-complete="off" :placeholder="$t('register.password')" @keyup.enter.native="registSubmit('registValidate')"></el-input>
						</el-form-item>
					</div>
					<div class="login-regist-input-wrap">
						<div class="password-icon"></div>
						<el-form-item prop="checkPass">
							<el-input type="password" v-model="registValidate.checkPass" auto-complete="off" :placeholder="$t('register.conformPwd')" @keyup.enter.native="registSubmit('registValidate')"></el-input>
						</el-form-item>
					</div>
					<div class="new-user-regist">
					    <span @click="registGoLogin('registValidate')">{{$t('register.returnLogin')}}</span>
					</div>
					<el-form-item>
				    	<el-button type="success" @click="registSubmit('registValidate')" :plain="true" class="login-regist-submit-btn">{{$t('register.register')}}</el-button>
				  	</el-form-item>
				</el-form>
			</div>

			<div class="login-regist-window" v-show="loginCtrl==='findPwd'">
				<div class="login-regist-title">{{$t('FindPwd.title')}}</div>
				<el-form :model="findPassword" ref="findPassword" :rules='findPasswordRules' class="login-regist-form">
					<div class="login-regist-input-wrap">
						<div class="email-icon"></div>
						<el-form-item prop="sendEmail">
							<el-input type="text" v-model="findPassword.sendEmail" auto-complete="off" :placeholder="$t('FindPwd.email')"></el-input>
						</el-form-item>
					</div>
					<div class="new-user-regist">
					    <span @click="findPwdGoLogin('findPassword')">{{$t('FindPwd.returnLogin')}}</span>
					</div>
					<p class="email-hint">{{$t('FindPwd.hint')}}</p>
					<el-form-item>
				    	<el-button type="success" @click="sendEmailSubmit('findPassword')" :plain="true" class="login-regist-submit-btn">{{$t('FindPwd.confirm')}}</el-button>
				  </el-form-item>
			  </el-form>
			</div>
		</div>
	</div>
</template>

<script>
import './login.scss'
export default {
	name:'login',
	data(){
		// 验证用户
      	var checkUser = (rule, value, callback) => {
	        if (!value) {
	          	return callback(new Error(this.$t('register.tip.noUser')));
	        } else {
	        	callback();
	        }
    	};
    	// 验证邮箱
      	var checkEmail = (rule, value, callback) => {
	        if (!value) {
	          	return callback(new Error(this.$t('register.tip.noEmail')));
	        }
	        setTimeout(() => {
		        if (!(/^([a-zA-Z0-9_-]){2,}@([a-zA-Z0-9_-]){2,}((\.[a-zA-Z0-9_-]{2,3}){1,2})$/).test(value)) {
		            callback(new Error(this.$t('register.tip.correctEmail')));
		        }else{
	              	callback();
	           }
	        }, 1000);
    	};
    	// 验证注册密码
      	var validatePass = (rule, value, callback) => {
        	if (value === '') {
          		callback(new Error(this.$t('register.tip.noPwd')));
        	} else {
          	if (this.registValidate.checkPass !== '') {
            	this.$refs.registValidate.validateField('checkPass');
          	}
          		callback();
        	}
      	};
      	var validatePass2 = (rule, value, callback) => {
        	if (value === '') {
        		callback(new Error(this.$t('register.tip.checkPwd')));
        	} else if (value !== this.registValidate.pass) {
          		callback(new Error(this.$t('register.tip.inconsistentPwd')));
        	} else {
          		callback();
        	}
      	};
      	// 验证登录密码
      	var loginPass = (rule, value, callback) => {
        	if (value === '') {
          		return callback(new Error(this.$t('register.tip.noPwd')));
        	} else {
          		callback();
        	}
      	};
		return{
			loginCtrl : 'login',
			languageDropdown : true,
			registValidate : {
				name : '',
				email : '',
				pass : '',
				checkPass : ''
			},
			users : {
				username : '',
				password : ''
			},
			findPassword : {
				sendEmail : ''
			},
			findPasswordRules : {
				sendEmail: [
					{ validator: checkEmail, trigger: 'blur' }
				]
			},
			usersRules : {
	          	username: [
	            	{ validator: checkUser, trigger: 'blur' }
	          	],
				password: [
					{ validator: loginPass, trigger: 'blur' }
				]
			},
	        registRules: {
	          	name: [
	            	{ validator: checkUser, trigger: 'blur' }
	          	],
				pass: [
					{ validator: validatePass, trigger: 'blur' }
				],
				checkPass: [
					{ validator: validatePass2, trigger: 'blur' }
				],
				email: [
					{ validator: checkEmail, trigger: 'blur' }
				]

	        }
		}
	},
	methods :{
		//  切换窗口重置内容
		registUser(formName){
			this.$refs[formName].resetFields()
			this.loginCtrl = 'regist'
		},
		findPwd(formName){
			this.$refs[formName].resetFields()
			this.loginCtrl = 'findPwd'
		},
		registGoLogin(formName){
			this.$refs[formName].resetFields()
			this.loginCtrl = 'login'
		},
		findPwdGoLogin(formName){
			this.registGoLogin(formName)
		},
		loginSubmit(formName){
	        this.$refs[formName].validate((valid) => {
	        	if (!valid) {
			        this.$message({message : this.$t('messageTip.loginFail'), duration : 2000, type: 'error'});
			        return false;
	        	}
				this.$http.Login(this.users)
				.then(data=>{
					window.localStorage.setItem('token', data.data.token)
					this.$router.push({path:'/'})
					this.$message({message : this.$t('messageTip.loginSuccess'), duration : 2000, type: 'success'});
					this.$http.GetUserMsg()
					.then(data=>{
						window.localStorage.setItem('userInfo', escape(JSON.stringify(data.data)))
						// this.$store.dispatch('navShow')
						this.$store.dispatch('jurisdiction', data.data)
					})
				})
				.catch(()=>{
					this.$message({message : this.$t('messageTip.loginFail'), duration : 2000, type: 'error'});
				})
	        });
		},
		registSubmit(formName){
	        this.$refs[formName].validate((valid) => {
	        	if (!valid) {
			        this.$message({message : this.$t('messageTip.registFail'), duration : 2000, type: 'error'});
			        return false;
	        	}
        		var registMsg = {
					name : this.registValidate.name,
					email : this.registValidate.email,
					password : this.registValidate.pass,
					info : {}
				}
				this.$http.Regist(registMsg)
				.then(data=>{
					window.localStorage.setItem('token', data.data.token)
					this.$router.push({path:'/'})
					this.$message({message : this.$t('messageTip.registSuccess'), duration : 2000, type: 'success'});
					this.$http.GetUserMsg()
					.then(data=>{
						// 登录成功
						window.localStorage.setItem('userInfo', escape(JSON.stringify(data.data)))
						this.$store.dispatch('jurisdiction', data.data)
					})
				})
				.catch(()=>{
					this.$message({message : this.$t('messageTip.registFail'), duration : 2000, type: 'error'});
				})
	        });
		},
		sendEmailSubmit(formName){
	        this.$refs[formName].validate((valid) => {
	        	if (!valid) {
					this.$message({message : this.$t('messageTip.sendFail'), duration : 2000, type: 'error'});
			        return false;
			    }
	        	this.$message({message : this.$t('messageTip.sendSuccess'), duration : 2000, type: 'success'});
	        });
		},
	    switchLanguage(command){
	    	if(command === 'Zh'){
		    	this.$i18n.locale = 'zh-CN'
		      localStorage.setItem('language', 'zh-CN')
	    	}else if(command === 'En'){
	    		this.$i18n.locale = 'en-US'
	    		localStorage.setItem('language', 'en-US')
	    	}else if(command === 'Ft'){
				this.$i18n.locale = 'zh-hant'
				localStorage.setItem('language', 'zh-hant')
			}
	    	this.$refs['users'].resetFields()
	    	this.$refs['registValidate'].resetFields()
	    	this.$refs['findPassword'].resetFields()
	    }
	}
}
</script>



